<template>
   <div class="iphoneframe-container">
      <BackButton @click="$emit('showAccountInfo')"/>
      <PreviewCardWithIphoneFrame
        class="w-100"
        :image="previewCardImage"
        :title="previewCardTitle"
        :desc="previewCardDesc"
        firstButtonText="Find related products"
        secondButtonText="Find all products"
        thirdButtonText="Contact us"
      />
   </div>
</template>

<script>
import PreviewCardWithIphoneFrame from "@/components/PreviewCardWithIphoneFrame.vue";
import BackButton from '@/components/BackButton';
export default {
  components:{
    BackButton,
    PreviewCardWithIphoneFrame
  },
   data() {
    return {
      pageActive: true,
      previewCardDefaults: {
        title: "Your Product Category",
        desc: "Description for production information will be provided here",
        image:
          "https://images.pexels.com/photos/2666467/pexels-photo-2666467.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500",
      },
      stageInfoText: {
        title: "Provide your contact Information",
        desc: "Fill your contact information once",
      },
      toggles: {
        contactUsCard: {
          title: "Feature 'Contact Us' Information",
          desc:
            "Enable this option will feature 'Contact us' information as a separate card in product list",
        },
        contactUsButton: {
          title: "Show 'Contact us' button inside product card",
          desc:
            "Enable this option will feature 'Contact us' information as a separate card in product list",
        },
        checkFacebook: {
          title: "Show 'Check our facebook' button inside product card",
          desc:
            "Enable this option will show 'Check our facebook' button in product card",
        },
        feature: {
          title: "Feature my company",
          desc:
            "Enable this option will show 'Check our facebook' button inproduct card",
        },
      },
      companyName: "",
      companyAddress: "",
      companyImage: "", //Both base64 and imageUrl supported
    };
  },
  computed: {
    previewCardTitle() {
      return this.companyName
        ? this.companyName
        : this.previewCardDefaults.title;
    },
    previewCardDesc() {
      return this.companyAddress
        ? this.companyAddress
        : this.previewCardDefaults.desc;
    },
    previewCardImage() {
      return this.companyImage
        ? this.companyImage
        : this.previewCardDefaults.image;
    },
  },
}
</script>
<style lang="scss">
.iphoneframe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:-1.5rem;
}
</style>